.DishDetail {
  width: 80%;
  padding: 10px 10px 10px 10px;
  -webkit-box-shadow: 4px 4px 4px -4px rgba(0, 0, 0, 0.75);
          box-shadow: 4px 4px 4px -4px rgba(0, 0, 0, 0.75);
}

.dish-detail-container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-item-align: end;
      align-self: flex-end;
  position: absolute;
}

.dish-image {
  margin: 5px;
  border: 1px solid #ccc;
  padding: 1em;
  text-align: center;
  -webkit-box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, 0.75);
          box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, 0.75);
}

.image-box-md {
  width: 120px;
  height: 120px;
}

.intro-box {
  padding: 1em;
  width: 45%;
  float: left;
  border: 1px solid #ccc;
  color: black;
}

.ingredient-box {
  padding: 1em;
  width: 45%;
  float: right;
  text-align: center;
  border: 1px solid #ccc;
  color: black;
  background-color: wheat;
  -webkit-box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, 0.75);
          box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, 0.75);
}

.image-box-xs {
  background-color: white;
  width: 50px;
  height: 50px;
}


.full-width {
  width: 100%;
  text-align: center;
}


